import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";

function AboutUs() {
  return (
    <div className="px-5 py-4 my-5">
      <h1 className="mb-4">About React</h1>
      <h4 className="mb-4">
        <b className="text-primary">React</b> is an open-source JavaScript
        library for building user interfaces. It is maintained by Facebook and a
        community of individual developers and companies. React can be used as a
        base in the development of single-page or mobile applications
      </h4>
      <h4 className="mb-4">
        <b className="text-primary">Routing</b> is the process of keeping the
        browser URL in sync with what’s being rendered on the page. React Router
        lets you handle routing declaratively. The declarative routing approach
        allows you to control the data flow in your application
      </h4>
    </div>
  );
}

export default AboutUs;
