import React from "react";
import styles from "./css/ReactRouter.module.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Container from "react-bootstrap/Container";
import {
  NavLink,
  Switch,
  Route,
  useParams,
  useRouteMatch,
} from "react-router-dom";

const userDetails = [
  {
    id: 1,
    title: "User 1",
    Name: "Kajal",
    Age: 21,
    DOB: "14 - 03 - 1998",
    City: "Vapi",
    State: "Gujarat",
    Country: "India",
  },
  {
    id: 2,
    title: "User 2",
    Name: "Amit",
    Age: 29,
    DOB: "14 - 03 - 1990",
    City: "Vapi",
    State: "Gujarat",
    Country: "India",
  },
  {
    id: 3,
    title: "User 3",
    Name: "Abhishek",
    Age: 22,
    DOB: "07 - 04 - 1997",
    City: "Vadodara",
    State: "Gujarat",
    Country: "India",
  },
  {
    id: 4,
    title: "User 4",
    Name: "Pooja",
    Age: 25,
    DOB: "18 - 12 - 1994",
    City: "Junagadh",
    State: "Gujarat",
    Country: "India",
  },
  {
    id: 5,
    title: "User 5",
    Name: "Keshav",
    Age: 35,
    DOB: "08 - 12 - 1991",
    City: "Ahmedabad",
    State: "Gujarat",
    Country: "India",
  },
  {
    id: 6,
    title: "User 6",
    Name: "Keshav",
    Age: 35,
    DOB: "28 - 11 - 1994",
    City: "Indore",
    State: "Madhya Pradesh",
    Country: "India",
  },
];

function GetUsersList(props) {
  return (
    <React.Fragment>
      <Container className="mx-auto my-5">
        <h1 className={`${styles.Title} mb-3`}>{props.user.title}</h1>
        <h5>Name: {props.user.Name}</h5>
        <h5>Age: {props.user.Age}</h5>
        <h5>DOB: {props.user.DOB}</h5>
        <h5>City: {props.user.City}</h5>
        <h5>State: {props.user.State}</h5>
        <h5>Country: {props.user.Country}</h5>
      </Container>
    </React.Fragment>
  );
}

function GetUsers() {
  return (
    <React.Fragment>
      {userDetails.map((user) => (
        <GetUsersList user={user} key={user.id} />
      ))}
    </React.Fragment>
  );
}

function Documentation() {
  return (
    <div>
      <h3 className="text-success">Child Routing</h3>
      <p>
        The useRouteMatch hook attempts to match the current URL in the same way
        that a Route would. It’s mostly useful for getting access to the match
        data without actually rendering a Route.
      </p>
      <p>
        The useLocation hook returns the location object that represents the
        current URL. You can think about it like a useState that returns a new
        location whenever the URL changes.
      </p>
    </div>
  );
}
function GetUsersParameters() {
  const { params } = useParams();
  return (
    <React.Fragment>
      <div className="pt-5">
        {params === undefined ? (
          <h4 className="text-danger">
            You should pass user detail to be searched as parameter
          </h4>
        ) : (
          <h4>
            Displaying user info with parameter:{" "}
            <b className="text-success">{params}</b>
          </h4>
        )}{" "}
      </div>
      {userDetails
        .filter(
          (user) =>
            user.Name === params ||
            JSON.stringify(user.id) === params ||
            user.City === params
        )
        .map((users) => (
          <GetUsersList user={users} key={users.id} />
        ))}
    </React.Fragment>
  );
}

function GetUsersByParams() {
  const { path, url } = useRouteMatch();
  return (
    <div className="py-5">
      <h1 className="text-primary">Search users Details</h1>
      <ul>
        <li>
          <NavLink
            to={`${url}/documentation`}
            activeClassName={`${styles.active}`}
          >
            <b>Documentation</b>
          </NavLink>
        </li>
        <li>
          <NavLink
            to={`${url}/get-users-by-id`}
            activeClassName={`${styles.active}`}
          >
            <b>Get Users Info Based On Id</b>
          </NavLink>
        </li>
        <li>
          <NavLink
            to={`${url}/get-users-by-name`}
            activeClassName={`${styles.active}`}
          >
            <b> Get Users Info Based On Name</b>
          </NavLink>
        </li>
        <li>
          <NavLink
            to={`${url}/get-users-by-city`}
            activeClassName={`${styles.active}`}
          >
            <b>Get Users Info Based On City</b>
          </NavLink>
        </li>
      </ul>
      <Switch>
        <Route path={`${path}/documentation`} component={Documentation}></Route>
        <Route
          path={`${path}/get-users-by-id/:params?`}
          component={GetUsersParameters}
        ></Route>
        <Route
          path={`${path}/get-users-by-name/:params?`}
          component={GetUsersParameters}
        ></Route>
        <Route
          path={`${path}/get-users-by-city/:params?`}
          component={GetUsersParameters}
        ></Route>
        <Route path={`${path}`}>
          <h1>Please choose a topic</h1>
        </Route>
      </Switch>
    </div>
  );
}
export { GetUsers, GetUsersByParams };
