import React, { useEffect } from "react";
import {
  BrowserRouter as Router,
  NavLink,
  Switch,
  Route,
} from "react-router-dom";
import styles from "./css/ReactRouter.module.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Home from "./Home";
import AboutUs from "./AboutUs";
import ContactUs from "./ContactUs";
import { GetUsers, GetUsersByParams } from "./GetUsers";
import QueryParameter from "./QueryParams";
import RedirectForm from "./ReactForm";
function Header() {
  return (
    <Navbar
      collapseOnSelect
      expand="lg"
      fixed="top"
      className={`p-4 mb-5 ${styles.Navbar}`}
    >
      <Navbar.Brand className={`${styles.Brand} font-weight-bolder text-light`}>
        <h1>React-Redirection</h1>
      </Navbar.Brand>
      <Navbar.Toggle
        aria-controls="responsive-navbar-nav"
        className="bg-light"
      />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="ml-auto font-weight-bold">
          <NavLink
            exact
            to="/"
            className={`${styles.Link} m-3`}
            activeClassName={`${styles.active}`}
          >
            RedirectionForm
          </NavLink>
          <NavLink
            to="/home"
            className={`${styles.Link} m-3`}
            activeClassName={`${styles.active}`}
          >
            Home
          </NavLink>
          <NavLink
            to="/about-us"
            className={`${styles.Link} m-3`}
            activeClassName={`${styles.active}`}
          >
            About Us
          </NavLink>
          <NavLink
            to="/contact-us"
            className={`${styles.Link} m-3`}
            activeClassName={`${styles.active}`}
          >
            Contact Us
          </NavLink>
          <NavLink
            to="/get-info"
            className={`${styles.Link} m-3`}
            activeClassName={`${styles.active}`}
          >
            Get Info
          </NavLink>
          <NavLink
            to="/get-info-by-params"
            className={`${styles.Link} m-3`}
            activeClassName={`${styles.active}`}
          >
            Get Info(params)
          </NavLink>
          <NavLink
            to="/query-param"
            className={`${styles.Link} m-3`}
            activeClassName={`${styles.active}`}
          >
            Query Params
          </NavLink>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
}

function Footer() {
  return (
    <>
      <div className={`${styles.Footer} p-4`}>
        <p className="my-auto">
          Author: Kajal Karmakar
          <br />
          <a
            href="mailto:kajal.karmakar@theonetechnologies.co.in"
            className="text-light"
          >
            kajal.karmakar@theonetechnologies.co.in
          </a>
        </p>
      </div>
      <p className={`${styles.Copy} ${styles.Footer} mb-0`}>
        <small>Copyright &copy; 2020</small>
      </p>
    </>
  );
}
function ReactRouter() {
  useEffect(() => {
    document.title = "Day-12-redirection";
  });
  return (
    <Router>
      <div>
        <Header />
        <br />
        <Container className={`mt-5 pt-5 ${styles.DisplayDetails}`}>
          <Switch>
            <Route exact path="/" component={RedirectForm}></Route>
            <Route path="/home" component={Home}></Route>
            <Route path="/about-us" component={AboutUs}></Route>
            <Route path="/contact-us" component={ContactUs}></Route>
            <Route path="/get-info" component={GetUsers}></Route>
            <Route
              path="/get-info-by-params"
              component={GetUsersByParams}
            ></Route>
            <Route path="/query-param" component={QueryParameter}></Route>
            <Route path="*">
              <h1 className="text-danger py-5">Not Found!!</h1>
            </Route>
          </Switch>
        </Container>
        <Footer />
      </div>
    </Router>
  );
}
export default ReactRouter;
