import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
class RedirectForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = { query: "", visible: false };
    this.handleOnChange = this.handleOnChange.bind(this);
    this.handleOnSubmit = this.handleOnSubmit.bind(this);
  }

  handleOnChange(event) {
    this.setState({ query: event.target.value, visible: true });
  }

  handleOnSubmit(event) {
    this.props.history.push("/" + this.state.query);
    event.preventDefault();
  }

  render() {
    return (
      <div className="px-5 py-5">
        <Form onSubmit={this.handleOnSubmit}>
          <Form.Group controlId="formBasicName">
            <Form.Label className="font-weight-bold mb-4">
              Enter the path you want to redirect
            </Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter path"
              name="query"
              value={this.state.query}
              onChange={this.handleOnChange}
            />
          </Form.Group>
        </Form>
        <Button variant="primary" type="submit" onClick={this.handleOnSubmit}>
          Submit
        </Button>
      </div>
    );
  }
}
export default RedirectForm;
