import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";

function Home() {
  return (
    <div className="px-5 py-4 my-5">
      <h1 className="mb-4">React Router</h1>
      <h4 className="mb-3">
        <b className="text-primary">React Router</b> is a standard routing
        library for React. When you need to navigate through a React application
        with multiple views, you’ll need a router to manage the URLs. React
        Router takes care of that, keeping your application UI and the URL in
        sync.
      </h4>
      <h4 className="mb-2">
        <b>React</b> is a popular library for creating single-page applications
        that are rendered on the client side
      </h4>
      <h4>React makes it painless to create interactive UIs.</h4>
    </div>
  );
}

export default Home;
