import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { useLocation } from "react-router-dom";
function useQuery() {
  return new URLSearchParams(useLocation().search);
}
function QueryParameter() {
  const QueryParams = useQuery();
  const query = [...QueryParams.entries()];
  const searchParam = query.map((searchparam, index) => (
    <li key={index}>
      <h4>
        You are visiting the page with{" "}
        <b className="text-primary">
          {searchparam[0]}={searchparam[1]}
        </b>
      </h4>
    </li>
  ));
  return (
    <div className="py-5">
      <h1 className="text-success">Query Parameters</h1>
      <ul>{searchParam}</ul>
    </div>
  );
}
export default QueryParameter;
